body.speakers-sial,
body.node1318742 {

  .speaker-sial > .block-title {
    @extend .main-title;
    position: relative;
    padding: 0 10px 80px;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 15px;

    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: 50%;
      display: block;
      height: 200%;
      width: 110vw;
      background-color: $lightgrey-3;
      transform: translate(-50%, -50%);
    }

    > * {
      color: $textcolor-2;
      font-size: 20px;
      font-weight: 300;
      line-height: 1.5;
      text-align: center;

      @include mq_min_width($desktop-small) {
        padding: 0 25%;
      }
    }
  }

  .contacts-list {
    padding: 30px 0;
    position: relative;
    

    h1 {
      @extend .main-title;
      margin-bottom: 12px;
      text-align: center;
    }

    .intro {
      position: relative;
      padding-bottom: 80px;
      margin-bottom: 15px;
      text-align: center;

      &::before {
        content: '';
        position: absolute;
        z-index: -1;
        top: -80px;
        left: 50%;
        display: block;
        height: 200%;
        min-height: 381px;
        width: 110vw;
        background-color: $lightgrey-3;
        transform: translate(-50%, -50%);
      }

      & + h2 {
        display: none;
      }
    }

    .cl-content {
      @extend %commercial-card-wrapper;
      margin-top: 50px;
    }

    .cl-item {
      @extend %commercial-card;
      flex-direction: column;
      max-width: 291px;
      margin: 15px auto;

      &.is__hidden {
        display: none;
      }

      @include mq_min_width(640px) {
        margin-left: 0;
        margin-right: 0;
      }

      &-photo {
        height: 212px;
        overflow: hidden;

        > img {
          width: 100%;
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;
        }
      }

      &-content {
        padding: 25px 30px 15px;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        min-height: 138px;
        background-color: #fff;
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
      }

      &-title {
        @extend %commercial-card-title;
        font-size: 18px;
        line-height: 1.55;
        order: 2;
        margin-bottom: 5px;
      }

      &-subtitle {
        order: 3;
        font-family: $font-1;
        font-weight: 300;
        font-size: 16px;
        line-height: 1.625;
        text-transform: lowercase;

        &:first-letter {
          text-transform: capitalize;
        }
      }

      &-email,
      &-phone,
      &-illust,
      &-social.additional-informations {
        display: none;
      }

      &-social {
        @extend %commercial-card-social;
        margin-bottom: 11px;
      }
    }
  }
}